.main {
  width: 100%;
  background-color: #051010;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.text {
  color: #85c0d7;
  background-color: #051010;
  text-align: center;
  line-height: 50px;
  font-size: 50px;
  font-style: italic;
  animation: blink 2.5s infinite;
}
.main img {
  height: 25vh;
  object-fit: cover;
  margin: 25px auto 50px;
}
